body {
    margin: 0;
    font-family: "Raleway", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.welcome-title {
    font-size: 8rem;
    font-weight: 900;
    line-height: 0.9;

    margin-bottom: 1rem;
}

.has-workflow-spacing {
    margin-bottom: 1rem;
}

.has-workflow-spacing.is-double {
    margin-bottom: 2rem;
}

.has-workflow-spacing.is-more {
    margin-bottom: 4rem;
}

.has-unique-background {
    background-color: #f0f0f0;
}

@media only screen and (max-width: 768px) {
    /* tablets and desktop */
    .welcome-title {
        font-size: 4rem;
    }
}

.video-container {
    display: inline-block;
    position: relative;
}

.video-overlay {
    position: absolute;
    left: 0px;
    bottom: 0px;
    margin: 10px;
    padding: 5px 5px;
    font-size: 20px;
    font-family: Helvetica;
    color: #fff;
}

.video-overlay .icon:hover {
    color: #fff;
    font-size: 30px;
}

#myVideo {
    width: 100%;
    height: 100%;
}

.timely {
    min-width: 10rem;
}

.test-bg2 {
    background: url("/images/undo_slider2.jpg") center top;
    background-size: cover;
    cursor: pointer;
}

.test-bg3 {
    background: url("/images/med_slider.jfif") center top;
    background-size: cover;
    cursor: pointer;
}

.selector {
    height: 40rem;
}

.selector .content {
    line-height: 40rem;
}

.selector .content:hover {
    background-color: gray;
    opacity: 0.2;
}

.selector .content:hover > a {
    color: white;
}

.selector .content > a {
    display: inline-block;
    vertical-align: middle;
    line-height: 5rem;
    text-align: center;

    text-decoration: none;
    font-size: 5rem;
}

.selector .content > div.button {
    display: inline-block;
    vertical-align: middle;
    text-align: center;
}

#selector .title {
    padding-top: 0.5em;
    margin-bottom: 0;
}
.radial-chart {
    position: relative;
    display: inline-block;
    transition: all 0.3s ease-in;
}
.radial-chart.no-progress .radial-chart-progress {
    opacity: 0;
}
.radial-chart-total {
    opacity: 0.3;
}
.radial-chart-progress {
    transform: rotate(90deg);
    transform-origin: center;
    transition: all 0.6s cubic-bezier(0.58, 0.16, 0.5, 1.14);
    transition-delay: 0.3s;
}

.radial-text {
    font-size: 18px;
    fill: #f0f0f0;
}
.section.is-parallax {
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    min-height: 40rem;

    padding: 0;
}

.section div.has-dot-overlay {
    padding: 5rem 0;
}

.test-bg {
    background: url("/images/shaking-hands.jpg") center top;
}

/* --- */

.chart {
    position: relative;
    display: inline-block;
    width: 140px;
    height: 140px;
    margin-top: 50px;
    margin-bottom: 0px;
    text-align: center;
}
.chart canvas {
    position: absolute;
    top: 0;
    left: 0;
}
.percent {
    position: absolute;
    display: inline-block;
    margin: 0px auto;
    z-index: 2;
    color: #f0f0f0;
    left: 40%;
    top: 40%;
    font-weight: 700;
    font-size: 18px;
}

.has-workflow-spacing {
    margin-bottom: 1rem;
}

.navbar-item img {
    max-height: 5rem;
}

#quick-action {
    margin-top: 0.2em;
    margin-right: 1em;
}

#quick-action.level {
    margin-bottom: 0;
}

#nav {
    margin-top: 0;
    padding-top: 0;
}

@media only screen and (max-width: 768px) {
    #quick-action {
        margin: auto;
    }
}
.info-padding {
    padding: 0 2rem;
}

.info-padding-title {
    margin-top: 2rem;
}

/* css for expandos */
.text {
    height: 75px;
    overflow: hidden;
}
.expand-container {
    position: relative;
    margin-bottom: 2rem;
}
label {
    position: absolute;
    top: 100%;
    cursor: pointer;
    font-size: 0.75em;
}
input {
    display: none;
}

label:after {
    content: "Show more";
}
input:checked + label:after {
    content: "Show less";
}
input:checked ~ div {
    height: 100%;
}
.map-responsive {
    position: relative;
    padding-top: 25%;
}
.map-responsive iframe {
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    position: absolute;
}
#hero-text {
    letter-spacing: 0.4em;
}

.hero-title {
    font-size: 6rem;
}

.has-bg-img {
    background-size: cover;
}

.button {
    border-radius: 0;
}

.has-dot-overlay {
    background: url("/images/dot.png");
    width: 100%;
    height: 100%; /*height of the image*/
}

.example-enter {
    opacity: 0.01;
}

.example-enter.example-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
}

.example-leave {
    opacity: 1;
}

.example-leave.example-leave-active {
    opacity: 0.01;
    transition: opacity 300ms ease-in;
}

@media only screen and (max-width: 768px) {
    /* tablets and desktop */
    .hero-title {
        font-size: 2.75rem;
    }
}
.slight-spacing {
    padding-bottom: 0.25rem;
}

.icons-with-text span {
    margin-right: 1rem;
}

.icons-with-text a:first-child {
    margin-right: 1rem;
}

.facebook {
    text-decoration: none;
}

.facebook:hover svg {
    color: #dbdbdb;
}

#cta.section.is-parallax {
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    min-height: 20rem;

    padding: 0;
}

#cta.section div.has-dot-overlay {
    padding: 10rem 0;
}

.background-img {
    background: url("/images/tattoo-art-body-wallpapers.jpg") center top;
}
